*{
    box-sizing:border-box;
    margin:0px;
    padding:0px;
}
.sentence{
    position: relative;
    margin-top: 80px;
    margin-bottom:100px;
    
}
.sentence h2{
    display: block;
    color:rgb(88, 26, 11);
    width:725px;
    margin:0 auto;
    text-align: left;
    margin-top: 100px;
    margin-bottom: 30px;
    letter-spacing: 0.2em;
    font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
}
.sentence p{
    display: block;
    color:rgb(88, 26, 11);
    width:725px;
    margin:0 auto;
    font-size:18px;
    margin-top: 60px;
    letter-spacing: 0.2em;
    line-height:200%;
    font-weight: bold;
    font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
}
.img1{
    position:absolute;
    top: 50px;
    left:-30px;
    width:300px;
    opacity: 0.1;
    
}
.img2{
    position:absolute;
    top: -150px;
    right:-30px;
    width:300px;
    opacity: 0.1;
    
}
