*{
    box-sizing:border-box;
    margin:0px;
    padding:0px;
}
.Item img{
    width:100%;
}
.Item{
    width:33%;
    
    font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
    padding:15px;
    float:left;

}
.Item p{
    letter-spacing: 0.51em;
    line-height:200%;
    color:rgb(88, 26, 11);
}
.Item h3{
    color:rgb(88, 26, 11);
    text-align: center;
    letter-spacing: 0.1em;
}

/* タブレット*/
.TItem img{
    width:100%;
}
.TItem{
    width:50%;
    font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
    padding:15px;
    float:left;
    height:500px;

}
.TItem p{
    letter-spacing: 0.51em;
    line-height:200%;
    color:rgb(88, 26, 11);
}
.TItem h3{
    color:rgb(88, 26, 11);
    text-align: center;
    letter-spacing: 0.1em;
}
