.header{
    height:200px;
    padding-top:50px;
}
.logo{
    position: relative;
    width:200px;
    margin: 0 auto;
    margin-bottom: 40px;
}
.logo p {
    position:absolute;
    font-size: 22px;
    top: 5px;
    left:60px;
    font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
}
.logo img{
    width:50px;
}

.menu li{
    list-style: none;
    float:left;
    width:20%;
    
}
.menu a{
    display: block;
    text-align: center;
    margin:0 auto;
    width:100%;
    font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
    cursor: pointer;
}
.menu a:hover{
    border-bottom:solid 1px;
    transition: 0.3s;
    color:brown;
}
.clear{
    clear:left;
}

/* スマホ　*/
.Kheader{
    height:60px;
}
.Klogo{
    position: relative;
    padding-top:10px;
}
.Klogo p {
    position:absolute;
    font-size: 20px;
    top: 18px;
    left:50px;
    font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
}
.Klogo img{
    width:40px;
}
.menubar{
    position:fixed;
    z-index: 10;
    top:0px;
    right:0px;
}