*{
    box-sizing:border-box;
    margin:0px;
    padding:0px;
}
.meisenWrapper h2{
    display: block;
    font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
    text-align: center;
    margin:0 auto;
    padding-left: 40px;
    color:rgb(88, 26, 11);
    margin-bottom:20px;
    letter-spacing: 2em;
    font-size: 30px;
}
.meisenWrapper{
    margin-bottom: 150px;
    position:relative;
    margin-left:40px;
    margin-right:40px;
}
.clear{
    clear:left;
}
.noren{
    position:absolute;
    width:250px;
    top:550px;
    left:0px;
    opacity: 0.1;
}
.pinpon{
    position:absolute;
    width:250px;
    top:550px;
    right:0px;
    opacity: 0.1;
}
.yu{
    position:absolute;
    width:250px;
    top:550px;
    left:0px;
    opacity: 0.1;
}
.kuchi{
    position:absolute;
    width:350px;
    top:500px;
    right:0px;
    opacity: 0.1;
}
/* タブレット*/
.TmeisenWrapper h2{
    display: block;
    font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;
    text-align: center;
    padding-left: 45px;
    color:rgb(88, 26, 11);
    margin-bottom:20px;
    letter-spacing: 2em;
    font-size: 30px;
}
.TmeisenWrapper{
    position:relative;
    margin-left:40px;
    margin-right:40px;
}
.clear{
    clear:left;
}
.Tnoren{
    position:absolute;
    width:30%;
    top:70%;
    right:20%;
    opacity: 0.1;
}
.Tpinpon{
    position:absolute;
    width:40%;
    top:500px;
    right:0px;
    opacity: 0.1;
}
.Tyu{
    position:absolute;
    width:30%;
    top:70%;
    right:20%;
    opacity: 0.1;
    z-index: 0;
}
.Tkuchi{
    position:absolute;
    width:40%;
    top:45%;
    right:0px;
    opacity: 0.1;
}

